import * as XLSX from "xlsx";

export const roundOffWeight = (weight: any) => {
    let decimal =  parseFloat((weight - Math.floor(weight)).toFixed(2));
    let finalWeight = weight;
    if(decimal !== 0 && weight > 20){
        finalWeight = Math.trunc(weight) + 1;
    }else if(decimal !== 0 && decimal <= 0.5){
        finalWeight = Math.trunc(weight) + 0.5;
    }else if(decimal !== 0 && decimal > 0.5){
        finalWeight = Math.trunc(weight) + 1;
    }

    return finalWeight;
}

export const getSumByKey = (arr: any, key: any) => {
    return new Promise((resolve, reject) => {
        let value = arr.reduce((accumulator:any, current:any) => accumulator + Number(current[key]), 0);
        resolve(value);
    });
}

export const getSumByKeyWithFilter = (arr: any, key: any, compareKey:any ='', compareVal:any = '') => {
    return new Promise((resolve, reject) => {
        let value = arr.filter((data:any) => data[compareKey] === compareVal).reduce((accumulator:any, current:any) => accumulator + Number(current[key]), 0);
        resolve(value);
    });
}

export const getSumByKeyWithFilters = (arr: any, key: any, compareKey1:any = '', compareVal1:any = '', compareKey2:any = '', compareVal2:any = '') => {
    return new Promise((resolve, reject) => {        
        let value = arr.filter((data:any) => data[compareKey1] === compareVal1 && data[compareKey2] === compareVal2).reduce((accumulator:any, current:any) => accumulator + Number(current[key]), 0);
        resolve(value);
    });
}

export const exportToExcel = (arr: any, fileName: any, sheetName:any = 'data') => {
    return new Promise((resolve, reject) => { 
        const header = Object.keys(arr[0]); // columns name
        let wscols = [];
        for (let i = 0; i < header.length; i++) {  // columns length added
            wscols.push({ wch: header[i].length + 5 })
        }       
        const wb = XLSX.utils.book_new();
        const ws = XLSX.utils.json_to_sheet(arr);
        ws["!cols"] = wscols;
        XLSX.utils.book_append_sheet(wb, ws, sheetName);
        XLSX.writeFile(wb, `${fileName}.xlsx`, { bookType: 'xlsx', type: 'buffer' });
        resolve(null)
    });
}