import { Component, OnInit, TemplateRef } from '@angular/core';
import { FormBuilder, FormGroup, FormArray, Validators } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';
import { NbSidebarService } from '@nebular/theme';
import { AdminService } from '../../services/admin/admin.service';
import { AccessService } from '../../services/access/access.service';
import { alphaRegx, mobileRegx, addressRegx, alnumSpecialRegx, pincodeRegx, urlRegx, dateRegx, numRegx, floatRegx, alnumRegx, emailRegx, nonHTMLRegx, adhaarRegx, panCardRegx, gstnRegx, passportRegx } from '../../helpers/regExp';
import { deleteConfirmAlert, changeConfirmAlert, successAlert, errorAlert, warning } from '../../helpers/sweetalert';
import { AuthenticateService } from '../../services/authentication/authenticate.service';
import { PricesService } from '../../services/prices/prices.service';
import { roundOffWeight } from '../../helpers/common';


@Component({
  selector: 'ngx-add-shipments',
  templateUrl: './add-shipments.component.html',
  styleUrls: ['./add-shipments.component.scss']
})
export class AddShipmentsComponent implements OnInit {

  addForm!: FormGroup;
  updateForm!: FormGroup;
  showModal! : boolean;
  submitted = false;
  list: any;
  roles: any;
  userAccess = {view: true, edit: false, delete: false};
  page: number = 1;
  pageSize: number = 25;
  totalCount: number = 0;
  search_string: string = '';
  pageSizes = ['25', '50', '100', '200'];
  displayMap: Boolean = false;
  selectedFiles: any;
  currentUserData: any;
  userBranch: any;
  branchesList: any;
  countriesList: any;
  currentDate: any = new Date().toISOString().split('T')[0];
  commodityDetails!: FormArray;
  commodityList: any;
  carriersList: any;
  pricesList: any = [];
  showBranchField: boolean = true;
  formErrors: { [key: string]: any; };
  showCustomDimentionsField: boolean = false;
  noOfPieces: number = 0;
  commoditiesLength: number = 0;

  constructor(
    private formBuilder: FormBuilder,
    private adminService: AdminService,
    private accessService: AccessService,
    private authenticateService: AuthenticateService,
    private pricesService: PricesService,
    private dialogService: NbDialogService,
    private sidebarService: NbSidebarService,
  ) { }

  ngOnInit(): void {
    const accessData = this.accessService.getActionAccess('add-shipments');
    if(accessData && accessData.length > 0){
      this.userAccess.edit = accessData[0].edit;
      this.userAccess.delete = accessData[0].delete;
    }
    
    this.toggleSidebar();
    this.getBranchesList();
    this.getCarriers();    
    this.getCountries();    
    this.getCommodityList();    
    
    this.currentUserData = this.authenticateService.currentUserValue;
    if(this.currentUserData.role_type !== 'Super-Admin'){
      this.userBranch = this.currentUserData.branch_id;
      this.showBranchField = false;
    }    

    this.addForm = this.formBuilder.group({
      branch: [this.userBranch, [Validators.required]],
      invoice_number: ['', [Validators.required]],
      date: [this.currentDate, [Validators.required]],
      service_type: ['', [Validators.required]],
      destination_country: ['US', [Validators.required]],
      no_of_pieces: ['', [Validators.required, Validators.pattern(numRegx), Validators.maxLength(4)]],
      boxes_3kg: ['', [Validators.pattern(numRegx)]],
      boxes_5kg: ['', [Validators.pattern(numRegx)]],
      boxes_10kg: ['', [Validators.pattern(numRegx)]],
      boxes_15kg: ['', [Validators.pattern(numRegx)]],
      boxes_custom: ['', [Validators.pattern(numRegx)]],
      custom_box_dimentions: [''],
      actual_weight: [0, [Validators.required, Validators.pattern(floatRegx)]],
      valumetric_weight: [0, [Validators.required, Validators.pattern(floatRegx)]],
      chargable_weight: [0, [Validators.required, Validators.pattern(floatRegx)]], 
      medicine_shipment: ['No', [Validators.required]],     
      product_type: ['Non Docs', [Validators.required]],
      basic_amount: [0, [Validators.required, Validators.pattern(floatRegx)]],
      gst_amount: [0, [Validators.required, Validators.pattern(floatRegx)]],
      other_amount: [0, [Validators.pattern(floatRegx)]],
      total_amount: [0, [Validators.required, Validators.pattern(floatRegx)]],
      mode_of_payment: ['CASH', [Validators.required]],
      transaction_id: ['', [Validators.required, Validators.pattern(nonHTMLRegx)]],
      sender_name: ['', [Validators.required, Validators.pattern(alnumSpecialRegx)]],
      sender_company_name: ['', [Validators.pattern(alnumRegx)]],
      sender_address1: ['', [Validators.required, Validators.pattern(addressRegx)]],
      sender_address2: ['', [Validators.required, Validators.pattern(addressRegx)]],
      sender_address3: ['', [Validators.pattern(addressRegx)]],
      sender_pincode: ['', [Validators.required, Validators.pattern(pincodeRegx)]],
      sender_country: ['India', [Validators.required, Validators.pattern(alphaRegx)]],
      sender_state: ['', [Validators.required, Validators.pattern(nonHTMLRegx)]],
      sender_city: ['', [Validators.required, Validators.pattern(nonHTMLRegx)]],
      sender_phone_number: ['', [Validators.required, Validators.pattern(mobileRegx)]],
      sender_email: ['', [Validators.pattern(emailRegx)]],
      sender_id_proof_type: ['', [Validators.required]],
      sender_id_proof_number: ['', [Validators.required, Validators.pattern(nonHTMLRegx)]],
      receiver_name: ['', [Validators.required, Validators.pattern(alnumRegx)]],
      receiver_company_name: ['', [Validators.pattern(alnumRegx)]],
      receiver_address1: ['', [Validators.required, Validators.pattern(addressRegx)]],
      receiver_address2: ['', [Validators.required, Validators.pattern(addressRegx)]],
      receiver_address3: ['', [Validators.pattern(addressRegx)]],
      receiver_pincode: ['', [Validators.required, Validators.minLength(5)]],
      receiver_state: ['', [Validators.required, Validators.pattern(nonHTMLRegx)]],
      receiver_city: ['', [Validators.required, Validators.pattern(nonHTMLRegx)]],
      receiver_phone_number: ['', [Validators.required, Validators.pattern(numRegx), Validators.minLength(9), Validators.maxLength(15)]],
      receiver_email: ['', [Validators.pattern(emailRegx)]],
      commodity_list: this.formBuilder.array([this.newCommodityFields()]),
      commodity_items_summery: ['', [Validators.required]],
      remarks: ['', [Validators.pattern(alnumSpecialRegx)]],
      kyc_doc: [''],
    });
    this.commodityDetails = this.addForm.get("commodity_list") as FormArray;
  }

  getAllErrors(form: FormGroup | FormArray): { [key: string]: any; } | null {
    let hasError = false;
    const result = Object.keys(form.controls).reduce((acc, key) => {
        const control = form.get(key);
        const errors = (control instanceof FormGroup || control instanceof FormArray)
            ? this.getAllErrors(control)
            : control.errors;
        if (errors) {
            acc[key] = errors;
            hasError = true;
        }
        return acc;
    }, {} as { [key: string]: any; });
    return hasError ? result : null;
  }

  toggleSidebar(): boolean {
    this.sidebarService.toggle(true, 'menu-sidebar');
    return false;
  }

  getCommodityFormGroup(index: number): FormGroup {
    this.commodityDetails = this.addForm.get('commodity_list') as FormArray;
    const formGroup = this.commodityDetails.controls[index] as FormGroup;
    return formGroup; 
  }

  commodity_list(): FormArray {
    return this.addForm.get("commodity_list") as FormArray;
  }

  newCommodityFields(): FormGroup {
    return this.formBuilder.group({
      box_number: ['', [Validators.required, Validators.pattern(numRegx)]],
      commodity_name: ['', [Validators.required]],
      hsn_code: ['', [Validators.required, Validators.pattern(alnumRegx)]],
      country: ['India', [Validators.required]],
      quantity: ['', [Validators.required, Validators.pattern(numRegx)]],
      uom: ['Pieces', [Validators.required]],
      unit_price: ['', [Validators.required, Validators.pattern(floatRegx)]],
      commodity_value: ['', [Validators.required, Validators.pattern(floatRegx)]],
    })
  }

  addCommodity() {  
    if((this.commodityDetails.value).length < this.noOfPieces){
      this.commodityDetails.push(this.newCommodityFields());
      this.commoditiesLength = (this.commodityDetails.value).length;
    }
  }

  removeCommodity(index: number) {
    this.commodityDetails.removeAt(index);
    this.commoditiesLength = (this.commodityDetails.value).length;
    let commodityNamesArray = this.commodityDetails.value.map(function (el: any) { return el.commodity_name.product_name; });
    this.addForm.controls['commodity_items_summery'].setValue(commodityNamesArray.join(','));
  }

  handleBoxNoChange = (event:any, index: number) => {
    if(!event.target.value || !numRegx.test(event.target.value)){
      this.getCommodityFormGroup(index).controls['box_number'].setErrors({pattern: true});
    }else if(event.target.value > this.addForm.value.no_of_pieces){
      this.getCommodityFormGroup(index).controls['box_number'].setErrors({maxValue: true});
    }else{
      this.getCommodityFormGroup(index).controls['box_number'].setErrors(null);
    }    
  }

  getBranchesList = async() => {
    let resp = await this.adminService.getData('getBranches');          
    if(resp && resp.responseCode === 1){
      this.branchesList = resp.branchesList;
    }   
  }

  getCarriers = async() => {
    let resp = await this.pricesService.getData('getCarriers');
    if(resp && resp.carriers){
      this.carriersList = resp.carriers;
    }
  }

  getCountries = async() => {
    let resp = await this.pricesService.getData('getCountries');
    if(resp && resp.countries){
      this.countriesList = resp.countries;
      this.handlePriceChange();
    }
  }

  getPricesUsingCountryAndCarrrier = async() => {
    if(this.addForm.value.service_type && this.addForm.value.destination_country){
      let reqData = {
        carrier_id: this.addForm.value.service_type,
        country_code: this.addForm.value.destination_country,
      }
      let resp = await this.pricesService.getData('getCarrrierZonePrices', reqData);
      if(resp && resp.prices && resp.prices.length > 0){
        this.pricesList = resp.prices;
        this.handlePriceChange();
        this.calculateWeight();
      }else{
        await warning("There are no prices available for this service type ! Please and prices and try again");
        this.addForm.setErrors({ 'invalid': true });
      }
    }
  }

  getCommodityList = async() => {
    let resp = await this.pricesService.getData('getCommodityList');
    if(resp && resp.commodity_list){
      this.commodityList = resp.commodity_list;
    }
  }

  calculateWeight = () => {
    if(this.addForm.value.no_of_pieces){
      if(this.addForm.value.boxes_custom){
        this.showCustomDimentionsField = true;
        if(this.addForm.value.custom_box_dimentions){
          this.addForm.controls['custom_box_dimentions'].setErrors(null);
        }else{
          this.addForm.controls['custom_box_dimentions'].setErrors({'required': true});
        }
      }else{
        this.showCustomDimentionsField = false;
        this.addForm.controls['custom_box_dimentions'].setErrors(null);
      }

      let noOfPieces: number = this.noOfPieces = parseInt(this.addForm.value.no_of_pieces);
      let actualWeight: any = this.addForm.value.actual_weight;
      let valumetricWeight: any = 0;
      let totalEnteredPieces: number = 0;
      let medicineShipment = this.addForm.value.medicine_shipment;
      let medicineShipmentValue = 'No';
      if(medicineShipment && medicineShipment === 'Yes' && noOfPieces === 1){
        medicineShipmentValue = 'Yes'
      }
      if(this.addForm.value.boxes_3kg){      
        totalEnteredPieces = totalEnteredPieces + parseInt(this.addForm.value.boxes_3kg);
        valumetricWeight = parseFloat(valumetricWeight) + ((30*21*21)/5000) * this.addForm.value.boxes_3kg;
      }
      if(this.addForm.value.boxes_5kg){
        totalEnteredPieces = totalEnteredPieces + parseInt(this.addForm.value.boxes_5kg);
        valumetricWeight = parseFloat(valumetricWeight) + ((36*26*26)/5000) * this.addForm.value.boxes_5kg;
      }
      if(this.addForm.value.boxes_10kg){
        totalEnteredPieces = totalEnteredPieces + parseInt(this.addForm.value.boxes_10kg);
        valumetricWeight = parseFloat(valumetricWeight) + ((40*35*35)/5000) * this.addForm.value.boxes_10kg;
      }
      if(this.addForm.value.boxes_15kg){
        totalEnteredPieces = totalEnteredPieces + parseInt(this.addForm.value.boxes_15kg);
        valumetricWeight = parseFloat(valumetricWeight) + ((60*35*35)/5000) * this.addForm.value.boxes_15kg;
      }
      if(this.addForm.value.boxes_custom){
        let customBoxes = parseInt(this.addForm.value.boxes_custom);
        totalEnteredPieces = totalEnteredPieces + customBoxes;
        if(this.addForm.value.custom_box_dimentions){
          let customBoxDimentions = this.addForm.value.custom_box_dimentions;
          if(customBoxes > 1){
            let dimentionValues = customBoxDimentions.split(',');
            if(dimentionValues.length === customBoxes){
              this.addForm.controls['custom_box_dimentions'].setErrors(null);
              dimentionValues.map((data:any) => {
                let dimentions = data.includes('x') ? data.split('x') : data.includes('X') ? data.split('X') : data.split('*');
                if(dimentions.length === 3){
                  let valumetricVal = (parseInt(dimentions[0])*parseInt(dimentions[1])*parseInt(dimentions[2])) / 5000;
                  valumetricWeight = parseFloat(valumetricWeight) + valumetricVal;
                }else{
                  this.addForm.controls['custom_box_dimentions'].setErrors({'pattern': true});
                }
              });
            }else{
              this.addForm.controls['custom_box_dimentions'].setErrors({'countMatch': true});
            }
          }else{
            let dimentions = customBoxDimentions.includes('x') ? customBoxDimentions.split('x') : customBoxDimentions.includes('X') ? customBoxDimentions.split('X') : customBoxDimentions.split('*');
            if(dimentions.length === 3){
              let valumetricVal = (parseInt(dimentions[0])*parseInt(dimentions[1])*parseInt(dimentions[2])) / 5000;
              valumetricWeight = parseFloat(valumetricWeight) + valumetricVal;
            }else{
              this.addForm.controls['custom_box_dimentions'].setErrors({'pattern': true});
            }
          }  
        }     
      }
      let totalWeight = actualWeight > valumetricWeight ? actualWeight : valumetricWeight;
      let finalWeight = roundOffWeight(totalWeight);

      this.addForm.controls['medicine_shipment'].setValue(medicineShipmentValue);
      this.addForm.controls['actual_weight'].setValue(actualWeight);
      this.addForm.controls['valumetric_weight'].setValue(valumetricWeight.toFixed(2));    
      this.addForm.controls['chargable_weight'].setValue(finalWeight);
      
      if(noOfPieces !== totalEnteredPieces){
        this.addForm.controls['no_of_pieces'].setErrors({'incorrect': true});
      }else{
        if(this.addForm.value.no_of_pieces && numRegx.test(this.addForm.value.no_of_pieces)){
          this.addForm.controls['no_of_pieces'].setErrors(null);
        }else{
          this.addForm.controls['no_of_pieces'].setErrors({'pattern': true});
        }
        let priceData = this.pricesList.length > 0 && this.pricesList.filter((data: any) => data.weight_from <= finalWeight && data.weight_to >= finalWeight);
        if(priceData && priceData.length > 0){
          let pricePerKG = medicineShipmentValue === 'Yes' ? priceData[0].medicine_price_per_kg : priceData[0].price_per_kg;
          let basicAmount = pricePerKG * finalWeight;
          let gstAmount = (basicAmount*18)/100;
          let otherAmount = parseInt(this.addForm.value.other_amount);
          let totalAmount = Math.round(basicAmount + gstAmount + otherAmount);
          this.addForm.controls['basic_amount'].setValue(basicAmount);
          this.addForm.controls['gst_amount'].setValue(gstAmount);
          this.addForm.controls['total_amount'].setValue(totalAmount);
        }
      }
    }
  }

  handlePriceChange = () => {    
    if(this.addForm.value.chargable_weight && (this.pricesList).length > 0){
      let finalWeight: number = this.addForm.value.chargable_weight;
      let medicineShipmentValue = this.addForm.value.medicine_shipment;
      let priceData = this.pricesList.filter((data: any) => data.weight_from <= finalWeight && data.weight_to >= finalWeight);
      if(priceData && priceData.length > 0){
        let pricePerKG = medicineShipmentValue === 'Yes' ? priceData[0].medicine_price_per_kg : priceData[0].price_per_kg;
        let basicAmount = pricePerKG * finalWeight;
        let gstAmount = (basicAmount*18)/100;
        let otherAmount = parseInt(this.addForm.value.other_amount);
        let totalAmount = Math.round(basicAmount + gstAmount + otherAmount);
        this.addForm.controls['total_amount'].setValue(totalAmount);
      }
    }else{
      this.addForm.controls['basic_amount'].setValue(0);
      this.addForm.controls['gst_amount'].setValue(0);
      this.addForm.controls['total_amount'].setValue(0);
    }
  }

  calculateCommodityValue = (index: number) => {
    if(this.commodityDetails.controls[index].value.quantity && this.commodityDetails.controls[index].value.unit_price){
      let commodityValue = parseFloat(this.commodityDetails.controls[index].value.quantity) * parseFloat(this.commodityDetails.controls[index].value.unit_price);
      this.getCommodityFormGroup(index).controls['commodity_value'].setValue(commodityValue);
    }
  }

  createNewCommodityOption = (newValue: any) => {    
    let data = {
      product_name: newValue,
      hsn_code: ''
    }
    this.commodityList.push(data);    
  }

  handleChangeCommodity = (commodityName: any, index: any) => {
    let commodityValue = this.commodityList.filter((data:any) => data.product_name === commodityName);
    if(commodityValue.length > 0){
      this.getCommodityFormGroup(index).controls['hsn_code'].setValue(commodityValue[0].hsn_code);
    }
    let commodityNamesArray = this.commodityDetails.value.map(function (el: any) { return el.commodity_name; });
    this.addForm.controls['commodity_items_summery'].setValue(commodityNamesArray.join(','));
  }

  changePageSize = (event: any) => {    
    this.pageSize = event.target.value;
  }

  openModal(dialog: TemplateRef<any>) {
    this.dialogService.open(
      dialog,
      {
        context: '',
        closeOnBackdropClick: false,
      });
  }

  getPincodeDetails = async(event: any, type = 'sender') => {
    try {
      let country = type === 'sender' ? 'IN' : this.addForm.value.destination_country;
      if(event.target.value && event.target.value.length >= 4){
        let pincodeDetails = await this.adminService.getPincodeDetails(event.target.value, country); 
        if(pincodeDetails && pincodeDetails.country){
          if(type === 'sender'){
            this.addForm.controls['sender_city'].setValue(pincodeDetails.city);
            this.addForm.controls['sender_state'].setValue(pincodeDetails.state);
          }else{
            this.addForm.controls['receiver_city'].setValue(pincodeDetails.city);
            this.addForm.controls['receiver_state'].setValue(pincodeDetails.state);
          }          
        }
      }else{
        if(type === 'sender'){
          this.addForm.controls['sender_city'].setValue(null);
          this.addForm.controls['sender_state'].setValue(null);
        }else{
          this.addForm.controls['receiver_city'].setValue(null);
          this.addForm.controls['receiver_state'].setValue(null);
        }
      }
    } catch (error) {
      if(type === 'sender'){
        this.addForm.controls['sender_city'].setValue(null);
        this.addForm.controls['sender_state'].setValue(null);
      }else{
        this.addForm.controls['receiver_city'].setValue(null);
        this.addForm.controls['receiver_state'].setValue(null);
      }
    }    
  }

  validateIDProof = () => {
    let idProofType = this.addForm.value.sender_id_proof_type;
    let idProofNo = this.addForm.value.sender_id_proof_number;
    if(idProofType && idProofNo){
      if(idProofType === 'AADHAR' && !adhaarRegx.test(idProofNo)){
        this.addForm.controls['sender_id_proof_number'].setErrors({'pattern': true});
      }else if(idProofType === 'PAN CARD' && !panCardRegx.test(idProofNo)){
        this.addForm.controls['sender_id_proof_number'].setErrors({'pattern': true});
      }else if(idProofType === 'GSTN' && !gstnRegx.test(idProofNo)){
        this.addForm.controls['sender_id_proof_number'].setErrors({'pattern': true});
      }else if(idProofType === 'PASSPORT' && !passportRegx.test(idProofNo)){
        this.addForm.controls['sender_id_proof_number'].setErrors({'pattern': true});
      }else{
        this.addForm.controls['sender_id_proof_number'].setErrors(null);
      }
    }
  }

  onFileSelect = async(event: any) => {
    this.selectedFiles = event.target.files;
  }

  get form() { return this.addForm.controls; }

  addFormSubmit = async() => {
    try {
      if(this.userAccess.edit){
        this.submitted = true;
        // stop here if form is invalid
        if (this.addForm.invalid) {
          this.submitted = false;
            return;
        }
        let formData = new FormData;
        for (const key of Object.keys(this.addForm.value)) {
          if(key != 'commodity_list'){
            let value = this.addForm.value[key];
            formData.append(key, value);
          }
        }
        formData.append('commodity_list', JSON.stringify(this.addForm.value.commodity_list));
        if(this.selectedFiles && this.selectedFiles.length > 0){
          formData.append('file_path', 'kyc-files');
          for(let i =0; i < this.selectedFiles.length; i++){
            formData.append("kyc_files", this.selectedFiles[i], this.selectedFiles[i]['name']);
          }
        }
        const addResp = await this.adminService.addOrUpdateData('addShipment', formData);
        if(addResp && addResp.responseCode === 1){
          await successAlert("Shipment added successfully");
          (<FormArray>this.addForm.get('commodity_list')).clear();
          this.addCommodity();
          this.addForm.reset();
          this.selectedFiles = '';
        }else if(addResp && addResp.responseCode === 0){
          await errorAlert(addResp.message);
        }else{
          await errorAlert("Shipment adding has failed ! Please try again");
        }
      }else{
        await errorAlert("Access Forbidden ! Please contact administrator");
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }    
    this.submitted = false;    
    document.getElementById("close_add_modal")?.click()
  }

}
