import { Component, OnInit, TemplateRef } from '@angular/core';
import { FormBuilder, FormGroup, FormArray, Validators } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';
import { NbSidebarService } from '@nebular/theme';
import { AdminService } from '../../services/admin/admin.service';
import { AccessService } from '../../services/access/access.service';
import { alphaRegx, mobileRegx, addressRegx, alnumSpecialRegx, pincodeRegx, urlRegx, numRegx, floatRegx, alnumRegx, emailRegx, nonHTMLRegx } from '../../helpers/regExp';
import { deleteConfirmAlert, successAlert, errorAlert, warning } from '../../helpers/sweetalert';
import { AuthenticateService } from '../../services/authentication/authenticate.service';
import { PricesService } from '../../services/prices/prices.service';
import { roundOffWeight } from '../../helpers/common';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'ngx-update-shipments',
  templateUrl: './update-shipments.component.html',
  styleUrls: ['./update-shipments.component.scss']
})
export class UpdateShipmentsComponent implements OnInit {

  updateForm!: FormGroup;
  showModal! : boolean;
  submitted = false;
  list: any;
  roles: any;
  userAccess = {view: true, edit: false, delete: false};
  page: number = 1;
  pageSize: number = 25;
  totalCount: number = 0;
  search_string: string = '';
  pageSizes = ['25', '50', '100', '200'];
  displayMap: Boolean = false;
  selectedFile: any;
  currentUserData: any;
  userBranch: any;
  branchesList: any;
  countriesList: any;
  currentDate: any = new Date().toISOString().split('T')[0];
  commodityDetails!: FormArray;
  commodityList: any;
  carriersList: any;
  pricesList: any = [];
  showBranchField: boolean = true;
  shipmentID: string;
  shipmentDetails: any;
  showCustomDimentionsField: boolean = false;
  noOfPieces: number = 0;
  commoditiesLength: number = 0;

  constructor(
    private formBuilder: FormBuilder,
    private adminService: AdminService,
    private accessService: AccessService,
    private authenticateService: AuthenticateService,
    private pricesService: PricesService,
    private dialogService: NbDialogService,
    private sidebarService: NbSidebarService,
    private route: ActivatedRoute,
    private router: Router,
  ) { }

  ngOnInit(): void {
    this.shipmentID = this.route.snapshot.paramMap.get('id');
    const accessData = this.accessService.getActionAccess('shipments-list');
    if(accessData && accessData.length > 0 && this.shipmentID){
      if(!accessData[0].edit){
        this.router.navigate(['/pages/access-denied']);
      }
      this.userAccess.edit = accessData[0].edit;
      this.userAccess.delete = accessData[0].delete;
    }else{
      this.router.navigate(['/pages/access-denied']);
    }
    
    this.toggleSidebar();
    this.getBranchesList();
    this.getCarriers();    
    this.getCountries();    
    this.getCommodityList();    
    
    this.currentUserData = this.authenticateService.currentUserValue;
    if(this.currentUserData.role_type !== 'Super-Admin'){
      this.userBranch = this.currentUserData.branch_id;
      this.showBranchField = false;
    }    

    this.getShipmentDetails();

    this.updateForm = this.formBuilder.group({
      id: [this.shipmentID, [Validators.required]],
      branch: [this.userBranch, [Validators.required]],
      invoice_number: ['', [Validators.required]],
      date: [this.currentDate, [Validators.required]],
      service_type: ['', [Validators.required]],
      destination_country: ['US', [Validators.required]],
      tracking_no1: ['', [Validators.required, , Validators.pattern(nonHTMLRegx)]],
      tracking_no2: ['', [Validators.pattern(nonHTMLRegx)]],
      tracking_no3: ['', [Validators.pattern(nonHTMLRegx)]],
      tracking_no4: ['', [Validators.pattern(nonHTMLRegx)]],
      no_of_pieces: ['', [Validators.required, Validators.pattern(numRegx), Validators.maxLength(4)]],
      boxes_3kg: ['', [Validators.pattern(numRegx)]],
      boxes_5kg: ['', [Validators.pattern(numRegx)]],
      boxes_10kg: ['', [Validators.pattern(numRegx)]],
      boxes_15kg: ['', [Validators.pattern(numRegx)]],
      boxes_custom: ['', [Validators.pattern(numRegx)]],      
      custom_box_dimentions: [''],      
      actual_weight: [0, [Validators.required, Validators.pattern(floatRegx)]],
      valumetric_weight: [0, [Validators.required, Validators.pattern(floatRegx)]],
      chargable_weight: [0, [Validators.required, Validators.pattern(floatRegx)]], 
      medicine_shipment: ['No', [Validators.required]],     
      product_type: ['Non Docs', [Validators.required]],
      basic_amount: [0, [Validators.required, Validators.pattern(floatRegx)]],
      gst_amount: [0, [Validators.required, Validators.pattern(floatRegx)]],
      other_amount: [0, [Validators.pattern(floatRegx)]],
      total_amount: [0, [Validators.required, Validators.pattern(floatRegx)]],
      mode_of_payment: ['CASH', [Validators.required]],
      transaction_id: ['', [Validators.required, Validators.pattern(nonHTMLRegx)]],
      sender_name: ['', [Validators.required, Validators.pattern(alnumSpecialRegx)]],
      sender_company_name: ['', [Validators.pattern(alnumRegx)]],
      sender_address1: ['', [Validators.required, Validators.pattern(addressRegx)]],
      sender_address2: ['', [Validators.required, Validators.pattern(addressRegx)]],
      sender_address3: ['', [Validators.pattern(addressRegx)]],
      sender_pincode: ['', [Validators.required, Validators.pattern(pincodeRegx)]],
      sender_country: ['India', [Validators.required, Validators.pattern(alphaRegx)]],
      sender_state: ['', [Validators.required, Validators.pattern(alphaRegx)]],
      sender_city: ['', [Validators.required, Validators.pattern(alphaRegx)]],
      sender_phone_number: ['', [Validators.required, Validators.pattern(mobileRegx)]],
      sender_email: ['', [Validators.pattern(emailRegx)]],
      sender_id_proof_type: ['', [Validators.required]],
      sender_id_proof_number: ['', [Validators.required, Validators.pattern(alnumSpecialRegx)]],
      receiver_name: ['', [Validators.required, Validators.pattern(alnumRegx)]],
      receiver_company_name: ['', [Validators.pattern(alnumRegx)]],
      receiver_address1: ['', [Validators.required, Validators.pattern(addressRegx)]],
      receiver_address2: ['', [Validators.required, Validators.pattern(addressRegx)]],
      receiver_address3: ['', [Validators.pattern(addressRegx)]],
      receiver_pincode: ['', [Validators.required, Validators.minLength(5)]],
      receiver_state: ['', [Validators.required, Validators.pattern(alphaRegx)]],
      receiver_city: ['', [Validators.required, Validators.pattern(alphaRegx)]],
      receiver_phone_number: ['', [Validators.required, Validators.pattern(mobileRegx)]],
      receiver_email: ['', [Validators.pattern(emailRegx)]],
      commodity_list: this.formBuilder.array([]),
      commodity_items_summery: ['', [Validators.required]],
      remarks: ['', [Validators.pattern(alnumSpecialRegx)]],
    });
    this.commodityDetails = this.updateForm.get("commodity_list") as FormArray;
  }

  toggleSidebar(): boolean {
    this.sidebarService.toggle(true, 'menu-sidebar');
    return false;
  }

  getCommodityFormGroup(index: number): FormGroup {
    this.commodityDetails = this.updateForm.get('commodity_list') as FormArray;
    const formGroup = this.commodityDetails.controls[index] as FormGroup;
    return formGroup; 
  }

  commodity_list(): FormArray {
    return this.updateForm.get("commodity_list") as FormArray;
  }

  newCommodityFields(): FormGroup {
    return this.formBuilder.group({
      box_number: ['', [Validators.required]],
      commodity_name: ['', [Validators.required]],
      hsn_code: ['', [Validators.required, Validators.pattern(alnumRegx)]],
      country: ['India', [Validators.required]],
      quantity: ['', [Validators.required, Validators.pattern(numRegx)]],
      uom: ['Pieces', [Validators.required]],
      unit_price: ['', [Validators.required, Validators.pattern(floatRegx)]],
      commodity_value: ['', [Validators.required, Validators.pattern(floatRegx)]],
    })
  }

  addCommodity() {
    if((this.commodityDetails.value).length < this.noOfPieces){
      this.commodityDetails.push(this.newCommodityFields());
      this.commoditiesLength = (this.commodityDetails.value).length;
    }
  }

  removeCommodity(index: number) {
    this.commodityDetails.removeAt(index);
    this.commoditiesLength = (this.commodityDetails.value).length;
    let commodityNamesArray = this.commodityDetails.value.map(function (el: any) { return el.commodity_name.product_name; });
    this.updateForm.controls['commodity_items_summery'].setValue(commodityNamesArray.join(','));
  }

  setCommodityFields(data: any) {
    (<FormArray>this.updateForm.get('commodity_list')).clear();    
    data.forEach((x: any) => {
      this.commodityDetails.push(this.formBuilder.group({ 
        box_number: x.box_number,
        commodity_name: x.commodity_name,
        hsn_code: x.hsn_code,
        country: x.country,
        quantity: x.quantity,
        uom: x.uom,
        unit_price: x.unit_price,
        commodity_value: x.commodity_value, 
      }))
    });
    this.commoditiesLength = (this.commodityDetails.value).length;
  }

  getShipmentDetails = async() => {
    let resp = await this.adminService.getData('getShipmentDetails', {id: this.shipmentID});          
    if(resp && resp.responseCode === 1){
      this.shipmentDetails = resp.info;              
      let data = {
        id: this.shipmentID,
        branch: this.shipmentDetails.branch_id,
        invoice_number: this.shipmentDetails.invoice_number,
        date: this.shipmentDetails.date,
        service_type: this.shipmentDetails.service_type,
        destination_country: this.shipmentDetails.destination_country,
        tracking_no1: this.shipmentDetails.tracking_no1,
        tracking_no2: this.shipmentDetails.tracking_no2,
        tracking_no3: this.shipmentDetails.tracking_no3,
        tracking_no4: this.shipmentDetails.tracking_no4,
        no_of_pieces: this.shipmentDetails.no_of_pieces,
        boxes_3kg: this.shipmentDetails.boxes_3kg,
        boxes_5kg: this.shipmentDetails.boxes_5kg,
        boxes_10kg: this.shipmentDetails.boxes_10kg,
        boxes_15kg: this.shipmentDetails.boxes_15kg,
        boxes_custom: this.shipmentDetails.boxes_custom,      
        custom_box_dimentions: this.shipmentDetails.custom_box_dimentions,      
        actual_weight: this.shipmentDetails.actual_weight,
        valumetric_weight: this.shipmentDetails.valumetric_weight,
        chargable_weight: this.shipmentDetails.chargable_weight, 
        medicine_shipment: this.shipmentDetails.medicine_shipment,     
        product_type: this.shipmentDetails.product_type,
        basic_amount: this.shipmentDetails.basic_amount,
        gst_amount: this.shipmentDetails.gst_amount,
        other_amount: this.shipmentDetails.other_amount,
        total_amount: this.shipmentDetails.total_amount,
        mode_of_payment: this.shipmentDetails.mode_of_payment,
        transaction_id: this.shipmentDetails.transaction_id,
        sender_name: this.shipmentDetails.sender_name,
        sender_company_name: this.shipmentDetails.sender_company_name,
        sender_address1: this.shipmentDetails.sender_address1,
        sender_address2: this.shipmentDetails.sender_address2,
        sender_address3: this.shipmentDetails.sender_address3,
        sender_pincode: this.shipmentDetails.sender_pincode,
        sender_country: this.shipmentDetails.sender_country,
        sender_state: this.shipmentDetails.sender_state,
        sender_city: this.shipmentDetails.sender_city,
        sender_phone_number: this.shipmentDetails.sender_phone_number,
        sender_email: this.shipmentDetails.sender_email,
        sender_id_proof_type: this.shipmentDetails.sender_id_proof_type,
        sender_id_proof_number: this.shipmentDetails.sender_id_proof_number,
        receiver_name: this.shipmentDetails.receiver_name,
        receiver_company_name: this.shipmentDetails.receiver_company_name,
        receiver_address1: this.shipmentDetails.receiver_address1,
        receiver_address2: this.shipmentDetails.receiver_address2,
        receiver_address3: this.shipmentDetails.receiver_address3,
        receiver_pincode: this.shipmentDetails.receiver_pincode,
        receiver_state: this.shipmentDetails.receiver_state,
        receiver_city: this.shipmentDetails.receiver_city,
        receiver_phone_number: this.shipmentDetails.receiver_phone_number,
        receiver_email: this.shipmentDetails.receiver_email,
        commodity_items_summery: this.shipmentDetails.commodity_items_summery,
        remarks: this.shipmentDetails.remarks,
        commodity_list: []     
      };
      if(this.shipmentDetails.boxes_custom && this.shipmentDetails.boxes_custom > 0){
        this.showCustomDimentionsField = true;
      }
      setTimeout(()=>{
        this.setCommodityFields(JSON.parse(this.shipmentDetails.commodity_list));
      }, 0);
      this.updateForm.setValue(data);                
    }else{
      this.router.navigate(['/pages/dashboard']);
    }   
  }

  getBranchesList = async() => {
    let resp = await this.adminService.getData('getBranches');          
    if(resp && resp.responseCode === 1){
      this.branchesList = resp.branchesList;
    }   
  }

  getCarriers = async() => {
    let resp = await this.pricesService.getData('getCarriers');
    if(resp && resp.carriers){
      this.carriersList = resp.carriers;
    }
  }

  getCountries = async() => {
    let resp = await this.pricesService.getData('getCountries');
    if(resp && resp.countries){
      this.countriesList = resp.countries;
      this.handlePriceChange();
    }
  }

  getPricesUsingCountryAndCarrrier = async() => {
    if(this.updateForm.value.service_type && this.updateForm.value.destination_country){
      let reqData = {
        carrier_id: this.updateForm.value.service_type,
        country_code: this.updateForm.value.destination_country,
      }
      let resp = await this.pricesService.getData('getCarrrierZonePrices', reqData);
      if(resp && resp.prices && resp.prices.length > 0){
        this.pricesList = resp.prices;
        this.handlePriceChange();
      }else{
        await warning("There are no prices available for this service type ! Please and prices and try again");
        this.updateForm.setErrors({ 'invalid': true });
      }
    }
  }

  getCommodityList = async() => {
    let resp = await this.pricesService.getData('getCommodityList');
    if(resp && resp.commodity_list){
      this.commodityList = resp.commodity_list;
    }
  }

  handleBoxNoChange = (event:any, index: number) => {
    if(!event.target.value || !numRegx.test(event.target.value)){
      this.getCommodityFormGroup(index).controls['box_number'].setErrors({pattern: true});
    }else if(event.target.value > this.updateForm.value.no_of_pieces){
      this.getCommodityFormGroup(index).controls['box_number'].setErrors({maxValue: true});
    }else{
      this.getCommodityFormGroup(index).controls['box_number'].setErrors(null);
    }    
  }

  calculateWeight = () => {
    let customBoxes = parseInt(this.updateForm.value.boxes_custom);
    if(customBoxes && customBoxes > 0){
      this.showCustomDimentionsField = true;
      if(this.updateForm.value.custom_box_dimentions){
        this.updateForm.controls['custom_box_dimentions'].setErrors(null);
      }else{
        this.updateForm.controls['custom_box_dimentions'].setErrors({'required': true});
      }
    }else{
      this.showCustomDimentionsField = false;
      this.updateForm.controls['custom_box_dimentions'].setErrors(null);
    }
    let noOfPieces: number = this.noOfPieces = parseInt(this.updateForm.value.no_of_pieces);    
    let actualWeight: any = this.updateForm.value.actual_weight;
    let valumetricWeight: any = 0;
    let totalEnteredPieces: number = 0;
    let medicineShipment = this.updateForm.value.medicine_shipment;
    let medicineShipmentValue = 'No';
    if(medicineShipment && medicineShipment === 'Yes' && noOfPieces === 1){
      medicineShipmentValue = 'Yes'
    }
    if(this.updateForm.value.boxes_3kg){      
      totalEnteredPieces = totalEnteredPieces + parseInt(this.updateForm.value.boxes_3kg);
      valumetricWeight = parseFloat(valumetricWeight) + ((30*21*21)/5000) * this.updateForm.value.boxes_3kg;
    }
    if(this.updateForm.value.boxes_5kg){
      totalEnteredPieces = totalEnteredPieces + parseInt(this.updateForm.value.boxes_5kg);
      valumetricWeight = parseFloat(valumetricWeight) + ((36*26*26)/5000) * this.updateForm.value.boxes_5kg;
    }
    if(this.updateForm.value.boxes_10kg){
      totalEnteredPieces = totalEnteredPieces + parseInt(this.updateForm.value.boxes_10kg);
      valumetricWeight = parseFloat(valumetricWeight) + ((40*34*34)/5000) * this.updateForm.value.boxes_10kg;
    }
    if(this.updateForm.value.boxes_15kg){
      totalEnteredPieces = totalEnteredPieces + parseInt(this.updateForm.value.boxes_15kg);
      valumetricWeight = parseFloat(valumetricWeight) + ((60*35*35)/5000) * this.updateForm.value.boxes_15kg;
    }
    if(customBoxes && customBoxes > 0){      
      totalEnteredPieces = totalEnteredPieces + customBoxes;
      if(this.updateForm.value.custom_box_dimentions){
        let customBoxDimentions = this.updateForm.value.custom_box_dimentions;
        if(customBoxes > 1){
          let dimentionValues = customBoxDimentions.split(',');
          if(dimentionValues.length === customBoxes){
            this.updateForm.controls['custom_box_dimentions'].setErrors(null);
            dimentionValues.map((data:any) => {
              let dimentions = data.includes('x') ? data.split('x') : data.includes('X') ? data.split('X') : data.split('*');
              if(dimentions.length === 3){
                let valumetricVal = (parseInt(dimentions[0])*parseInt(dimentions[1])*parseInt(dimentions[2])) / 5000;
                valumetricWeight = parseFloat(valumetricWeight) + valumetricVal;
              }else{
                this.updateForm.controls['custom_box_dimentions'].setErrors({'pattern': true});
              }
            });
          }else{
            this.updateForm.controls['custom_box_dimentions'].setErrors({'countMatch': true});
          }
        }else{
          let dimentions = customBoxDimentions.includes('x') ? customBoxDimentions.split('x') : customBoxDimentions.includes('X') ? customBoxDimentions.split('X') : customBoxDimentions.split('*');
          if(dimentions.length === 3){
            let valumetricVal = (parseInt(dimentions[0])*parseInt(dimentions[1])*parseInt(dimentions[2])) / 5000;
            valumetricWeight = parseFloat(valumetricWeight) + valumetricVal;
          }else{
            this.updateForm.controls['custom_box_dimentions'].setErrors({'pattern': true});
          }
        }  
      }     
    }
    let totalWeight = actualWeight > valumetricWeight ? actualWeight : valumetricWeight;
    let finalWeight = roundOffWeight(totalWeight);

    this.updateForm.controls['medicine_shipment'].setValue(medicineShipmentValue);
    this.updateForm.controls['actual_weight'].setValue(actualWeight);
    this.updateForm.controls['valumetric_weight'].setValue(valumetricWeight.toFixed(2));    
    this.updateForm.controls['chargable_weight'].setValue(finalWeight);
    
    if(noOfPieces !== totalEnteredPieces){
      this.updateForm.controls['no_of_pieces'].setErrors({'incorrect': true});
    }else{
      if(this.updateForm.value.no_of_pieces && numRegx.test(this.updateForm.value.no_of_pieces)){
        this.updateForm.controls['no_of_pieces'].setErrors(null);
      }else{
        this.updateForm.controls['no_of_pieces'].setErrors({'pattern': true});
      }
      let priceData = this.pricesList.length > 0 && this.pricesList.filter((data: any) => data.weight_from <= finalWeight && data.weight_to >= finalWeight);
      if(priceData && priceData.length > 0){
        let pricePerKG = medicineShipmentValue === 'Yes' ? priceData[0].medicine_price_per_kg : priceData[0].price_per_kg;
        let basicAmount = pricePerKG * finalWeight;
        let gstAmount = (basicAmount*18)/100;
        let otherAmount = parseInt(this.updateForm.value.other_amount);
        let totalAmount = Math.round(basicAmount + gstAmount + otherAmount);
        this.updateForm.controls['basic_amount'].setValue(basicAmount);
        this.updateForm.controls['gst_amount'].setValue(gstAmount);
        this.updateForm.controls['total_amount'].setValue(totalAmount);
      }
    }
  }

  handlePriceChange = () => {    
    if(this.updateForm.value.chargable_weight && (this.pricesList).length > 0){
      let finalWeight: number = this.updateForm.value.chargable_weight;
      let medicineShipmentValue = this.updateForm.value.medicine_shipment;
      let priceData = this.pricesList.filter((data: any) => data.weight_from <= finalWeight && data.weight_to >= finalWeight);
      if(priceData && priceData.length > 0){
        let pricePerKG = medicineShipmentValue === 'Yes' ? priceData[0].medicine_price_per_kg : priceData[0].price_per_kg;
        let basicAmount = pricePerKG * finalWeight;
        let gstAmount = (basicAmount*18)/100;
        let otherAmount = parseInt(this.updateForm.value.other_amount);
        let totalAmount = Math.round(basicAmount + gstAmount + otherAmount);
        this.updateForm.controls['total_amount'].setValue(totalAmount);
      }
    }else{
      this.updateForm.controls['basic_amount'].setValue(0);
      this.updateForm.controls['gst_amount'].setValue(0);
      this.updateForm.controls['total_amount'].setValue(0);
    }
  }

  calculateCommodityValue = (index: number) => {
    if(this.commodityDetails.controls[index].value.quantity && this.commodityDetails.controls[index].value.unit_price){
      let commodityValue = parseFloat(this.commodityDetails.controls[index].value.quantity) * parseFloat(this.commodityDetails.controls[index].value.unit_price);
      this.getCommodityFormGroup(index).controls['commodity_value'].setValue(commodityValue);
    }
  }

  createNewCommodityOption = (newValue: any) => {    
    let data = {
      product_name: newValue,
      hsn_code: ''
    }
    this.commodityList.push(data);    
  }

  handleChangeCommodity = (commodityName: any, index: any) => {
    let commodityValue = this.commodityList.filter((data:any) => data.product_name === commodityName);
    if(commodityValue.length > 0){
      this.getCommodityFormGroup(index).controls['hsn_code'].setValue(commodityValue[0].hsn_code);
    }
    let commodityNamesArray = this.commodityDetails.value.map(function (el: any) { return el.commodity_name; });
    this.updateForm.controls['commodity_items_summery'].setValue(commodityNamesArray.join(','));
  }

  changePageSize = (event: any) => {    
    this.pageSize = event.target.value;
  }

  openModal(dialog: TemplateRef<any>) {
    this.dialogService.open(
      dialog,
      {
        context: '',
        closeOnBackdropClick: false,
      });
  }

  getPincodeDetails = async(event: any, type = 'sender') => {
    try {
      let country = type === 'sender' ? 'IN' : this.updateForm.value.destination_country;
      if(event.target.value && event.target.value.length >= 4){
        let pincodeDetails = await this.adminService.getPincodeDetails(event.target.value, country); 
        if(pincodeDetails && pincodeDetails.country){
          if(type === 'sender'){
            this.updateForm.controls['sender_city'].setValue(pincodeDetails.city);
            this.updateForm.controls['sender_state'].setValue(pincodeDetails.state);
          }else{
            this.updateForm.controls['receiver_city'].setValue(pincodeDetails.city);
            this.updateForm.controls['receiver_state'].setValue(pincodeDetails.state);
          }          
        }
      }else{
        if(type === 'sender'){
          this.updateForm.controls['sender_city'].setValue(null);
          this.updateForm.controls['sender_state'].setValue(null);
        }else{
          this.updateForm.controls['receiver_city'].setValue(null);
          this.updateForm.controls['receiver_state'].setValue(null);
        }
      }
    } catch (error) {
      if(type === 'sender'){
        this.updateForm.controls['sender_city'].setValue(null);
        this.updateForm.controls['sender_state'].setValue(null);
      }else{
        this.updateForm.controls['receiver_city'].setValue(null);
        this.updateForm.controls['receiver_state'].setValue(null);
      }
    }    
  }

  get form() { return this.updateForm.controls; }

  updateFormSubmit = async() => {
    try {
      if(this.userAccess.edit){
        this.submitted = true;
        // stop here if form is invalid
        if (this.updateForm.invalid) {
          this.submitted = false;
            return;
        }
        // let formData = new FormData;
        // for (const key of Object.keys(this.updateForm.value)) {
        //   if(key != 'commodity_list'){
        //     let value = this.updateForm.value[key];
        //     formData.append(key, value);
        //   }
        // }
        // console.log('this.updateForm.value.commodity_list', this.updateForm.value.commodity_list);      
        // formData.append('commodity_list', JSON.stringify(this.updateForm.value.commodity_list));
        // if(this.selectedFile && this.selectedFile.length > 0){
        //   formData.append('file_path', 'kyc-files');
        //   formData.append("kyc_file", this.selectedFile[0], this.selectedFile[0]['name']);
        // }
        let reqData = {...this.updateForm.value};
        const addResp = await this.adminService.addOrUpdateData('updateShipmentDetails', reqData);
        if(addResp && addResp.responseCode === 1){
          await successAlert("Details updated successfully");
        }else if(addResp && addResp.responseCode === 0){
          await errorAlert(addResp.message);
        }else{
          await errorAlert("Details updating has failed ! Please try again");
        }
      }else{
        await errorAlert("Access Forbidden ! Please contact administrator");
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }    
    this.submitted = false;
    document.getElementById("close_add_modal")?.click()
  }

}
